.homesecWrap {
  
  .article2 {
    .row {
      ul {
        display: flex; flex-wrap : wrap; padding: 30px 0;
        li { width: 33.33%; padding: 20px;
          a { display: flex;
            img { width: 30%; margin-right: 10px; border-radius: 10px;}
            div {align-self : center}
          }
          
        }
      }
      .liftStyle {
        h2 {font-size: 20px; text-align: center; font-size: 32px;}
        ul { display: flex;
          li { width: 33.33%; padding: 0 10px;
            img{ display: block; width:100%; height: auto;}
          }
        }
      }
    }
  }
  .article3 {
    background: #333; color: #fff; padding: 100px 0px;
    .row {
      display:flex;  
      div { width: 52%; position: relative; 
        img { 
          width: 100%; padding: 10px 10px 10px ; 
          border-radius: 30px; vertical-align: middle; }
        p { 
          position: absolute; bottom: 20px; z-index: 20;
          margin: 0 10px; padding-left: 20px; 
          border-radius: 0px 0px 30px 30px;
        }
      &.right { width: 48%;
        display: flex; flex-direction:column; justify-content: space-between;
        div { width: 100%; }
        div.rightFst { 
          display:flex; flex-direction: row; 
        }
      }  
      }
    }
  }
}